
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


const emptyObject = {
name: '',
newname: '',
mobile: '',
newmobile: '',
vcode: '',
oldpassword: '',
password: '',
repassword: ''
}

export default{
mixins:[common],
components:{
Cell,
Popup,
Group,
XInput,
XButton
},
data () {
return {
popup_setting: false,
action: '',
tId: null,
send: {
text: '发送验证码',
loading: false
},
user: util.copy(emptyObject)
}
},
mounted () {
this.init_data()
},
methods: {
sendCapt() {
if(!this.checkTel()) {
return false
}
this.getCapt()
},
getCapt() {
let time = 59
if(this.send.loading) {
return false
}
this.send = {loading: true,text: '60s'}
this.tId = setInterval(() => {
if(time < 10) {
time = '0' + time
}
this.send.text = time + 's'
time --
if(time < 0) {
clearInterval(this.tId)
this.send = {loading: false,text: '发送验证码'}
}
}, 1000)
this.$rest('open/vcode')
.post({mobile:this.user.newmobile,type:'change'})
.success(response => {

})
.error(response => {
this.alert('获取验证码失败', response.body.message)
clearInterval(this.tId)
this.send = {loading: false,text: '发送验证码'}
})
},
checkTel() {
let tel = this.user.newmobile
if(tel === '') {
this.alert('请先输入手机号', '手机号不能为空')
return false
}
if(tel !== '' && !(/^1[35789]{1}\d{9}$/).test(tel)) {
this.alert('请重新输入手机号', '手机号格式不正确')
return false
}
return true
},
show_popup (action) {
this.action = action
this.popup_setting = true
},
hide_popup () {

this.user.newmobile = ''
this.user.newname = this.user.name
this.popup_setting = false
this.send = {
text: '发送验证码',
loading: false
}
window.clearInterval(this.tId)
},
edit_profile () {
if(this.user.password  === this.user.repassword){
let params = {action: this.action}

if(this.action == 'edit_name'){
params.name = this.user.newname
}

else if(this.action == 'edit_mobile'){
params.mobile = this.user.newmobile
params.vcode = this.user.vcode
}

else{
params.oldpassword = this.user.oldpassword
params.password = this.user.password
params.repassword = this.user.repassword
}
this.$http.post('users/edit_profile',params)
.then(response=>{
if(this.action == 'edit_name') {
this.user.name = this.user.newname
this.$store.commit('updateUserInfo',{user:this.user,action:'changeName'})
}else if(this.action == 'edit_mobile'){
this.user.mobile = this.user.newmobile
this.$store.commit('updateUserInfo',{user:this.user,action:'changeMobile'})
}
this.toast('修改成功','success')
this.hide_popup()
}).catch(response=>{
console.log(response)
this.toast(response.body.message,'warn')
})
}else{
this.toast('两次密码不一致','warn')
}
},
init_data () {
let user_info = this.$store.state.user.info
if(user_info){
this.user.name = user_info.name
this.user.newname = user_info.name
this.user.mobile = user_info.mobile
}
},
logout () {
this.$http.get('logout')
.then(response=>{
this.$store.commit('clearLoginInfo')
this.$router.push({path: '../login'})
}).catch(response=>{
this.$Message.error(response.body.message)
})
},
},
computed: {
required () {
let user = this.user
if(this.action == 'edit_name'){
return user.newname.length === 0
}else if (this.action == 'edit_mobile'){
return !(/^1[35789]{1}\d{9}$/).test(user.newmobile)||user.vcode.length === 0
}else {
return user.oldpassword.length === 0 || user.password.length === 0 || user.repassword.length === 0
}
},
}
}



